html {
  box-sizing: border-box;
  font-size: 12px;
}

*, *:after, *:before {
  box-sizing: inherit;
}

@media (min-width: 300px) {
  html {
    font-size: 14px;
  }
}
@media (min-width: 480px) {
  html {
    font-size: 16px;
  }
}
@media (min-width: 1024px) {
  html {
    font-size: 18px;
  }
}
body {
  font-size: 1rem;
  font-family: "Roboto", sans-serif;
  line-height: 1.5;
  background-color: #ebe7de;
}

img,
video,
audio {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  text-decoration: none;
  color: #000000;
}

p {
  font-size: clamp(0.82rem, 0.11vw + 0.81rem, 0.89rem);
}

h1, h2, h3 {
  margin: 0 0 3.125rem 0;
  line-height: 1.2;
  font-size: clamp(1.6rem, 0.76vw + 1.51rem, 2.11rem);
}

h1 {
  font-weight: 900;
}

h2 {
  font-weight: 700;
}

h3 {
  font-weight: 700;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

[class$=__contenedor] {
  max-width: 75rem;
  width: 90%;
  margin: 0 auto;
}

.bg-primario {
  background-color: #5e311e;
}

.text-center {
  text-align: center;
}

.vertical-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.texto-degradado {
  color: transparent;
  background: linear-gradient(to right, #5e311e 0%, #c39c63 100%);
  background-clip: text;
}

.header {
  position: relative;
}
.header::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #000000;
  opacity: 0.6;
}
@media (min-width: 768px) {
  .header {
    min-height: 37.5rem;
  }
}

.header__contenedor {
  position: relative;
  padding: 3.125rem 0;
}

.header__slogan {
  color: #ffffff;
  text-transform: uppercase;
  margin: 0;
}
@media (min-width: 768px) {
  .header__slogan {
    margin-top: 6.25rem;
  }
}

/* inicio */
.header--inicio {
  background-size: cover;
  background-position-x: 15%;
}
@media (min-width: 768px) {
  .header--inicio {
    background-position: 0% 20%;
  }
}

.notavif.notwebp .header--inicio {
  background-image: url(../img/header_inicio.jpg);
}

.webp .header--inicio {
  background-image: url(../img/header_inicio.webp);
}

.avif .header--inicio {
  background-image: url(../img/header_inicio.avif);
}

/* nosotros */
.header--nosotros {
  background-size: cover;
  background-position: 18% 80%;
}

.notavif.notwebp .header--nosotros {
  background-image: url(../img/header_nosotros.jpg);
}

.webp .header--nosotros {
  background-image: url(../img/header_nosotros.webp);
}

.avif .header--nosotros {
  background-image: url(../img/header_nosotros.avif);
}

/* proceso */
.header--proceso {
  background-size: cover;
  background-position: center 80%;
}

.notavif.notwebp .header--proceso {
  background-image: url(../img/header_proceso.jpg);
}

.webp .header--proceso {
  background-image: url(../img/header_proceso.webp);
}

.avif .header--proceso {
  background-image: url(../img/header_proceso.avif);
}

/* menu */
.header--menu {
  background-size: cover;
  background-position: center 80%;
}

.notavif.notwebp .header--menu {
  background-image: url(../img/header_menu.jpg);
}

.webp .header--menu {
  background-image: url(../img/header_menu.webp);
}

.avif .header--menu {
  background-image: url(../img/header_menu.avif);
}

/* galeria */
.header--galeria {
  background-size: cover;
  background-position: top center;
}

.notavif.notwebp .header--galeria {
  background-image: url(../img/header_galeria.jpg);
}

.webp .header--galeria {
  background-image: url(../img/header_galeria.webp);
}

.avif .header--galeria {
  background-image: url(../img/header_galeria.avif);
}

/* contacto */
.header--contacto {
  background-size: cover;
  background-position: 40% center;
}

.notavif.notwebp .header--contacto {
  background-image: url(../img/header_contacto.jpg);
}

.webp .header--contacto {
  background-image: url(../img/header_contacto.webp);
}

.avif .header--contacto {
  background-image: url(../img/header_contacto.avif);
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 1.875rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.barra__logo {
  width: 18.75rem;
}
@media (min-width: 768px) {
  .barra__logo {
    width: 9.375rem;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
  gap: 0.3125rem;
  width: 100%;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    gap: 0.625rem;
    width: auto;
  }
}

.navegacion__link {
  font-size: clamp(1.28rem, 0.45vw + 1.22rem, 1.58rem);
  text-transform: uppercase;
  color: #ffffff;
  font-weight: 700;
  width: 100%;
  text-align: center;
}
.navegacion__link:hover {
  color: #c39c63;
}
@media (min-width: 768px) {
  .navegacion__link {
    font-size: clamp(0.82rem, 0.11vw + 0.81rem, 0.89rem);
  }
}

.navegacion__link--cp {
  color: #c39c63;
}

.main {
  padding: 3.125rem 1.25rem;
  background-color: #ffffff;
  max-width: 75rem;
  width: 90%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .main {
    position: relative;
    margin-top: -6.25rem;
    padding: 6.25rem;
  }
}

.heading {
  text-transform: uppercase;
  color: #5e311e;
}
.heading::after {
  content: "";
  display: block;
  margin: 0 auto;
  width: 6.25rem;
  height: 6.25rem;
  background-image: url(../img/cafe.svg);
  background-repeat: no-repeat;
  background-size: cover;
}

.heading__span {
  display: block;
  font-size: clamp(1.02rem, 0.24vw + 0.99rem, 1.19rem);
  color: #c39c63;
}

.heading--blanco,
.heading__span--blanco {
  color: #ffffff;
}

.heading--blanco::after {
  display: none;
}

.iconos {
  margin-top: 3.125rem;
}

.iconos__listado {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .iconos__listado {
    flex-direction: row;
    justify-content: space-around;
  }
}

.iconos__icono {
  width: 7.5rem;
}

.iconos__p {
  font-family: "Bona Nova", serif;
  font-size: clamp(1.6rem, 0.76vw + 1.51rem, 2.11rem);
  color: #5e311e;
}

.galeria {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}
@media (min-width: 768px) {
  .galeria {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .galeria {
    grid-template-columns: repeat(4, 1fr);
  }
}

.galeria__div {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .galeria__div {
    margin: 0;
  }
}
@media (min-width: 1024px) {
  .galeria__div:first-of-type {
    grid-column: 1/2;
    grid-row: 1/3;
  }
  .galeria__div:nth-of-type(2) {
    grid-column: 2/4;
    grid-row: 1/2;
  }
  .galeria__div:nth-of-type(4) {
    grid-column: 2/3;
    grid-row: 2/4;
  }
  .galeria__div:nth-of-type(5) {
    grid-column: 3/5;
    grid-row: 2/3;
  }
  .galeria__div:nth-of-type(7) {
    grid-column: 1/2;
    grid-row: 3/5;
  }
  .galeria__div:nth-of-type(6) {
    grid-column: 3/5;
    grid-row: 3/4;
  }
  .galeria__div:nth-of-type(8) {
    grid-column: 2/5;
    grid-row: 4/5;
  }
  .galeria__div:nth-of-type(9) {
    grid-column: 1/3;
    grid-row: 5/6;
  }
  .galeria__div:last-of-type {
    grid-column: 3/5;
    grid-row: 5/6;
  }
}

.galeria__imagen {
  width: 480px;
}
@media (min-width: 768px) {
  .galeria__imagen {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.menu {
  background-image: url(../img/menu_arriba.svg), url(../img/menu_abajo.svg);
  background-repeat: no-repeat;
  background-position: center 2%, center 96%;
  background-size: 200%;
  padding: 9.375rem 3.125rem 12.5rem 3.125rem;
  background-color: #5e311e;
}
@media (min-width: 768px) {
  .menu {
    background-position: center 5%, center 93%;
    background-size: 70%;
    padding: 9.375rem 0 12.5rem 0;
  }
}

.menu__grid {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}
@media (min-width: 768px) {
  .menu__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1.25rem;
  }
}

.menu__heading {
  color: #ffffff;
  margin-bottom: 1.875rem;
}

.menu__item {
  display: flex;
  flex-direction: column;
}
@media (min-width: 480px) {
  .menu__item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.menu__producto {
  font-size: clamp(1.28rem, 0.45vw + 1.22rem, 1.58rem);
}

.menu__nombre {
  color: #ffffff;
  display: block;
}

.menu__descripcion {
  color: #c39c63;
}

.menu__precio {
  color: #ffffff;
  font-size: clamp(1.6rem, 0.76vw + 1.51rem, 2.11rem);
  font-weight: 900;
  margin-top: 0;
}
@media (min-width: 480px) {
  .menu__precio {
    margin: 0;
  }
}

.testimoniales__contenedor {
  background-color: #ffffff;
  padding: 3.125rem 1.25rem;
}
@media (min-width: 768px) {
  .testimoniales__contenedor {
    padding: 6.25rem 1.25rem;
    margin-top: -6.25rem;
  }
}

.testimoniales__testimonios {
  max-width: 31.25rem;
  margin: 0 auto;
  background-color: #ebe7de;
  padding: 1.25rem 2.5rem;
  border-radius: 0.625rem;
}

.testimoniales__autor {
  text-align: right;
  color: #5e311e;
  font-size: clamp(1.28rem, 0.45vw + 1.22rem, 1.58rem);
  font-family: "Bona Nova", serif;
}

.footer {
  padding-bottom: 1.25rem;
  background-color: #5e311e;
}

.footer__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ffffff;
  gap: 1.25rem;
  padding: 3.125rem 0;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.25rem;
    align-items: start;
  }
}

.footer__heading {
  color: #c39c63;
  margin-bottom: 1.25rem;
  font-family: "Bona Nova", serif;
  font-size: clamp(1.28rem, 0.45vw + 1.22rem, 1.58rem);
  text-transform: uppercase;
}

.footer__copy {
  margin: 1.25rem 0 0 0;
  color: #ffffff;
}

.sobreNosotros {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2.5rem;
}
@media (min-width: 768px) {
  .sobreNosotros {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2.5rem;
    align-items: start;
  }
}

.sobreNosotros__imagen {
  transform: rotate(-6deg);
  border: 1.875rem solid #ffffff;
  box-shadow: 9px 10px 5px -7px rgba(0, 0, 0, 0.35);
}

.sobreNosotros__frase {
  margin: 0;
  padding: 0.625rem;
  border-left: 0.3125rem solid #5e311e;
  font-size: clamp(1.02rem, 0.24vw + 0.99rem, 1.19rem);
}

.proceso__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 3.125rem;
  padding: 0 1.25rem;
}
@media (min-width: 768px) {
  .proceso__container {
    flex-direction: row;
    gap: 3.125rem;
    padding: 0;
  }
}

.proceso__container:last-of-type {
  margin-bottom: 0;
}

@media (min-width: 768px) {
  .proceso__container:nth-of-type(2n) {
    flex-direction: row-reverse;
  }
}

@media (min-width: 768px) {
  .proceso__imagen,
.proceso__contenido {
    flex: 1;
  }
}

.proceso__heading {
  margin: 0.625rem 0;
  text-align: left;
  text-transform: uppercase;
  color: #5e311e;
}
.proceso__heading::after {
  content: "";
  display: block;
  height: 5px;
  width: 50%;
  background-color: #c39c63;
}
@media (min-width: 768px) {
  .proceso__heading {
    margin: 0 0 0.625rem 0;
  }
}

.proceso__parrafo {
  text-align: left;
}

@media (min-width: 768px) {
  .notavif.notwebp .nota {
    background-image: url(../img/cafe.jpg);
  }
}

@media (min-width: 768px) {
  .webp .nota {
    background-image: url(../img/cafe.webp);
  }
}

@media (min-width: 768px) {
  .avif .nota {
    background-image: url(../img/cafe.avif);
  }
}

.nota {
  text-align: left;
}
@media (min-width: 768px) {
  .nota {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    background-repeat: no-repeat;
    aspect-ratio: 16/9;
    background-position: right bottom;
    background-size: 40%;
  }
}

@media (min-width: 768px) {
  .pbcero {
    padding-bottom: 0;
  }
}

.nota__parrafo {
  font-size: clamp(1.02rem, 0.24vw + 0.99rem, 1.19rem);
}

@media (min-width: 768px) {
  .notavif.notwebp .reservaciones {
    background-image: url(../img/granos_cafe.jpg);
  }
}

@media (min-width: 768px) {
  .webp .reservaciones {
    background-image: url(../img/granos_cafe.webp);
  }
}

@media (min-width: 768px) {
  .avif .reservaciones {
    background-image: url(../img/granos_cafe.avif);
  }
}

@media (min-width: 768px) {
  .reservaciones {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    background-repeat: no-repeat;
    background-position: right center;
    background-size: 40%;
  }
}

.formulario__campo {
  margin-bottom: 1.25rem;
}
.formulario__campo:last-of-type {
  margin-bottom: 0;
}

.formulario__label {
  display: block;
  text-transform: uppercase;
  font-size: clamp(1.28rem, 0.45vw + 1.22rem, 1.58rem);
  font-weight: 700;
  text-align: left;
}

.formulario__input {
  padding: 1rem;
  width: 100%;
  border: 5px solid #5e311e;
